.article-date, .article-category .article-category-link, .archive-year-wrap .archive-year, .archive-article-date {
    text-decoration: none;
    color: #5c6b72!important;
}

.article-meta::before, .article-meta::after {
    content: "";
    display: table;
    clear: both;
}
.article-inner  .blur-img {
    z-index: 0;
}

.articles .article-entry, .articles .article-footer {
    margin-top: 0;
}


.article-inner .blur-img img {
    position: absolute;
    top: 0;
    height: 100%;;
    object-fit: cover;
    background-position-x: center;
    background-position-y: center;
    background-size: cover;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    filter: blur(1.875rem) brightness(0.8);
    transform: scale(1.2);
}
.lazy.loaded {
    opacity: 1;
    transition: opacity 0.5s;
}
.article-entry a {
    color: #f5f5f5;
}




.article-share-link {
    cursor: pointer;
    float: right;
    margin-left: 20px;
}
.comments-number {
    margin: 0 10px;
}
.post-date, .post-meta, .post-meta a {
    color: #f5f5f5;
    font-size: 12px;
}
.post-date {
   
    font-family: 'Noto Serif SC','Source Han Serif SC','Source Han Serif','source-han-serif-sc','PT Serif','SongTi SC','MicroSoft Yahei',Georgia,serif;
}
.article-entry a,.article-inner header,.article-code h3{
letter-spacing: 0.06rem;
    text-shadow: 0 0.1875rem 0.3125rem rgba(0, 0, 0, 0.3);
}
.article-code i {
    vertical-align: middle;
    margin-right: 5px;
    color: #ff4646;
    font-size: 14px;
}
.article-code .icon-time{
    color: aqua;
}

.article-code .icon-file{
    margin-left: 4px;
}
.article-tag-list {
    list-style: none;
    padding-left: 0;
    margin-left: 0;
    margin: 0;
}

.article-inner:hover {
    box-shadow: 0 5px 10px 5px rgba(110,110,110,.4);
}


.article-entry i {
    font-size: 25px;
    color: #666;
}

 
.article-inner .article-img a{
    height: 100%;
}
.article-img{
    clip-path: polygon(0 0, 100% 0%, 94% 100%, 0% 100%);
    margin: 0;
    
    overflow: hidden;
  
    
    z-index: 2;
    width: 35%;
}

.article-code header{
    font-size: 12px;
    display: flex;
    justify-content: space-between;
}

.article-code {
    width: 65%;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 3rem 2rem;
    z-index: 2;
}

.flex-row-reverse .flex-row-reverse .article-img{
    clip-path: polygon(6% 0, 100% 0%, 100% 100%, 0% 100%) !important;
    margin: 0;
    border-radius: 0;
}

.post-list-show {
    animation: post-list-show .5s;
    -webkit-animation: post-list-show .5s;
    opacity: 1!important;
}
.post-list-thumb-left .article-img{
    border-radius: 10px 0 0 10px;
}

.article-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    transition: all .6s;
   
        
       
        display: block;
        background-repeat: no-repeat;
        background-size: cover;
        
       
    }
    .article-code h3 a,.article-code .article-entry a {
        width: 100%;
        display: inline-block;
    }

    .article-inner:hover .article-img img {
        transform: scale(1.05) rotate(1deg);
    }
.article-code h3{
    font-size: 1.5rem;
    font-weight: bold;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    word-break: break-all;
   
  
   
    word-wrap: break-word;
    /* margin: 1em 0; */
}
.article-code h3 a{
    color:#fff;
}

.article-inner{
    /* padding: 46px 0;
    border-bottom: 1px  #e6e6e6 solid ; */
    display: flex;
    overflow: hidden;
    width: 100%;
    height: 14.5rem;
    position: relative;
    margin: 20px 0 40px;
    border-radius: 10px;
    background-color: rgba(255,255,255,0);
    box-shadow: 0 1px 20px -6px rgba(0,0,0,.5);
    opacity: 0;
    transition: box-shadow .3s ease;
}
.flex-row-reverse {
    flex-direction: row-reverse!important;
}
.article-tag-list:before {
    content: "\f153";
    font-family: feathericon;
    margin-right: 0.5rem;
}
.article-tag-list .article-tag-list-item {
    display: inline-block;
    padding-right: 1rem;
}
.article-share-link:before {
    content: "\f14f";
    font-family: feathericon;
    padding-right: 6px;
}
.article-tag-list .article-tag-list-link {
    text-decoration: none;
}
.article-footer a{
    color: #127ea9;
}


  
  
  .outer {
  padding-top: 3em;
    width: 100%;
    
    margin: auto;
  }


  .article-title:hover,.post-meta a:hover {
    text-decoration: underline;
    color: #FE9600;
}

h1.fes-title, h1.main-title {
    color: #666;
    font-size: 16px;
    font-weight: 400;
    padding-bottom: 5px;
    margin-bottom: 30px;
    border-bottom: 1px dashed #ececec;
}

.article-inner .article-entry a {
    line-height: 1.8rem;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    word-break: break-all;
    min-height: 3.625rem;
}

.article-inner .article-title {
    font-size:1.5rem;
    font-weight: bold;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    overflow: hidden;
    word-break: break-all;
  }

@media (max-width: 576px){
    .article-inner:hover {
        box-shadow: unset;
    }
    .article-inner .article-title{
        font-size: 18px;
    font-weight: bold;
    }
    .article-inner h3 {
        font-size: 18px;
        font-weight: bold;
    }

    
    .article-inner .article-entry a {
        display: -webkit-box;
        font-size: 12px;
        line-height: unset;
        min-height: auto;
    }
   
    .article-inner .blur-img {
        display: none;
    }
    .article-inner .article-code{
        
        padding: 1rem;
        min-width: calc(100% - 80px - 0.5rem);
    }
    .article-inner .post-meta {
        width: 100%;
    }
    .article-inner .post-meta span:last-child{
        float: right;
    }

    
    .article-inner .article-img{
        width: unset;
        clip-path: unset;
    display: flex;
    align-items: center;
    margin: 0.5rem 0 0.5rem 0.5rem;
    }
    .article-inner .article-img img {
        border-radius: 6px;
        width: 80px;
        height: 80px;
    }
    .article-code .icon-file{
        color: #18cace;
    }
    .push-time-text,.arlt-read-text,.article-inner .post-date{
        display: none;
    }
.article-inner {
    height: 8.5rem;
 
   
    margin-bottom: 1rem;
}
.article-inner .post-bottom{
    display: none;
}
.article-entry p{
    max-height: 67.5px;
}



.article-img {
    border-radius: 10px!important;
}

.article-inner .article-code{
    
    min-width: calc(100% - 80px - 0.5rem);
}


}

@media screen and (max-width: 768px) and (min-width: 576px){
    .article-inner {
    height: 10.5rem;
    
}
.article-inner .article-title{
    font-size: 1.25rem;
}
.article-inner .blur-img {
    display: none;
}
.article-inner .article-code {
    padding: 1.25rem;
}
.article-inner .article-entry a {
    -webkit-line-clamp: 2;
    min-height: unset;

}

.article-inner .article-img  img{
    border-radius: 10px;
}
.article-inner .article-img {
    clip-path: unset;
    
}
}

@media screen and (max-width: 768px){
    .article-inner .article-img {
    clip-path: unset;
    display: flex;
    align-items: center;
    margin: 0.5rem 0 0.5rem 0.5rem;
}
.article-inner .article-img a{
    clip-path: unset;
    display: flex;
    align-items: center;
    margin: 0.5rem 0.5rem 0.5rem 0;
}


}






