#content {
    -webkit-animation: main 1s;
    animation: main 1s;
}
.site-content {
    max-width: 800px;
    padding: 0 10px;
    margin-left: auto;
    margin-right: auto;
    background-color: #fff;
}
.links {
    margin-bottom: 80px;
}

.download, .navigator i:hover, .link-title, .links ul li:hover, #pagination a:hover, .comment-respond input[type='submit']:hover {
    border-color: #fe9600;
}

.link-title span.fake-title {
    padding-left: 10px;
    border-left: 3px solid orange;
}

.link-title {
    font-weight: 400;
    color: #6d6d6d;
    padding-left: 0;
    border-left: none;
    margin: 50px 0 20px;
}

.links ul {
    margin: 0;
    list-style: none;
    padding: 0;
    width: 100%;
    display: inline-block;
}

.links ul li {
    z-index: 0;
    width: 32%;
    float: left;
    border: 1px solid #ececec;
    padding: 10px 30px;
    margin: 4px;
    position: relative;
    overflow: hidden;
    transition: all .3s;
    border-radius: 10px;
    height: 100px;
}
@media (max-width: 860px){
    .links ul li {
        width: 48.6%;
    }
}

@media (max-width: 630px){
.links ul li {
    width: 100% !important;
}
}
.feature i, .feature-title span, .download, .navigator i:hover, .links ul li:before, .ar-time i, span.ar-circle, .object, .comment .comment-reply-link, .siren-checkbox-radio:checked + .siren-checkbox-radioInput:after {
    background: #fe9600;
}
.links ul li:before {
    content: "";
    background-color: #ffebc6;
    transform: skew(45deg,0);
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    left: -60px;
    z-index: -1;
    transition: all .5s;
}


.links ul li img {
    float: right;
    box-shadow: inset 0 0 10px #000;
    padding: 5px;
    opacity: 1;
    transform: rotate(0);
    -webkit-transform: rotate(0);
    -moz-transform: rotate(0);
    -o-transform: rotate(0);
    -ms-transform: rotate(0);
    transition: all ease 1s;
    -webkit-transition: all ease 1s;
    -moz-transition: all ease 1s;
    -o-transition: all ease 1s;
    margin-top: 5px;
    width: 65px;
    height: 65px;
    padding: 2px;
    border-radius: 100%;
}

.links-tips{
    word-break: break-all;
}


span.sitename {
    color: orange;
    padding-bottom: 10px;
    display: block;
    transition: all .3s;
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
}

.links ul li:hover {
    color: #27323a;
    border: 1px solid orange;
}
.linkdes {
    color: #949494;
    font-size: 13px;
    padding: 10px 0;
    border-top: 1px dashed #ddd;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    line-height: 25px;
    transition: all .5s;
}
.friendlink .leftbox  .dark-bg {
    padding: 0  1rem 0 1rem;
    border-radius: 1%
}


.links ul li:hover:before {
    width: 180%;
}

.links ul li:hover img {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
}


.links-tips {
    font-family: 'noto serif sc','source han serif sc','source han serif',source-han-serif-sc,'pt serif',songti sc,microsoft yahei,Georgia,serif;
}