


.tiptop-inner{
    /* position: absolute;
    top: 50%;
    left: 50%;
    min-width: 254px;
    text-align: center;
    transform: translate(-50%, -65%); */

    position: relative;
    max-width: 800px;
    padding: 0 10px;
    top: 49.3%;
    left: 50%;
    transform: translate(-50%,-50%);
    -webkit-transform: translate(-50%,-50%);
    text-align: center;
    z-index: 99;
    -webkit-transition: .4s ease all;
    -moz-transition: .4s ease all;
    -o-transition: .4s ease all;
    transition: .4s ease all;
}

.allMargintop{
    margin-bottom: 3em;
}
.tiptop-inner h1 a, .tiptop-inner {
    color: #fff;
    text-decoration: none;
    z-index: 99;
}



.tiptop-inner h1 a{
    position: relative;
    color: #fff;
    mix-blend-mode: lighten;
    display: block;
    width: 100%;
    font-size: 80px;
}

@media (max-width: 780px){
    .tiptop-inner{
        display: none;
    }
}


#banner_wave_1,#banner_wave_2{
 height: 65px;
 background: url("https://cdn.jsdelivr.net/gh/moezx/cdn@3.5.1/img/Sakura/images/wave1.png") repeat-x;
position: absolute;
bottom: 0;
width: 400%;
z-index: 4;
opacity: 1;
transition-property:opacity,bottom;
transition-duration: .4s,.4s;
}

#banner_wave_2{
    height: 80px;
    background: url("https://cdn.jsdelivr.net/gh/moezx/cdn@3.5.1/img/Sakura/images/wave2.png") repeat-x;
}



.tiptop-inner h1 a:after{
    left: 1px;
    text-shadow: -1px 0 #00a7e0;
}
.tiptop-inner h1 a:before{
    left: -1px;
    text-shadow: 1px 0 #ff3f1a
}


.tiptop-inner h1 a:hover:before {
    text-shadow: 4px 0 #ff3f1a;
    animation: glitch-loop-1 .8s infinite ease-in-out alternate-reverse;
}

.tiptop-inner h1 a:hover:after {
    text-shadow: -5px 0 #00a7e0;
    animation: glitch-loop-2 .8s infinite ease-in-out alternate-reverse;
}


.tiptop-inner h1 a:before, .tiptop-inner h1 a:after {
    content: attr(data-text);
    position: absolute;
    top: 0;
    width: 100%;
    background: rgba(0,0,0,0);
    clip: rect(0,0,0,0);
    height: 100%;
}
.tiptop-inner h1{
    font-size: 2em;
    font-family: 'Ubuntu',sans-serif;
    text-align: center;
}

.anchor{
    cursor: pointer;
}
.tiptop-inner .brand {
    padding-top: 1em;
    width: 4rem;
    margin: 0 auto;
    display: block;
}
.tipimgBox:before{
background-image: url(https://cdn.jsdelivr.net/gh/moezx/cdn@3.1.9/img/Sakura/images/dot.gif);
content:'';position:absolute;top:0;bottom:0;left:0;right:0;z-index:3;background-attachment:fixed
}
.tiptop-learn-more {
    position: absolute;
    z-index: 1;
    bottom: -3rem;
    right: 0;
    left: 0;
    text-align: center;
}


.fe-quote-left::before,.fe-quote-right::before{
    color: #1e3e3f;
    padding-right: 2px;
    vertical-align: top;
}

.fe-quote-left::before{
    content: '\f14b';
}
.fe-quote-right::before{
    content: '\f14c';
    
}

.tiptop-learn-more a > .fe {
    font-size: 35px;
    color: #adb5bd;
}
.tiptop-learn-more a > .fe:hover {
    color: #1e3e3f;
}
.fe {
    display: inline-block;
    font: normal normal normal 16px feathericon;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}



.fe-mouse:before {
    content: "\f142";
}

.tipimgBox{
    animation: header 1s;
    height: 100vh;
    overflow: hidden;
}




    .header-info {
        
        margin: auto;
        font-size: 16px;
        color: #eaeadf;
        background: rgba(0,0,0,.5);
        padding: 15px;
        display: inline-block;
        margin-top: 22px;
        letter-spacing: 0;
        line-height: 30px;
        border-radius: 10px;
        box-sizing: initial;
        white-space: nowrap;
    }

    .header-info:before {
        content: "";
        position: absolute;
        top: 84px;
        left: 30%;
        margin-left: -15px;
        border-width: 15px;
        border-style: solid;
        border-color: transparent transparent rgba(0,0,0,.5) transparent;
    }

    .header-info p {
        margin: 0;
        font-family: 'Ubuntu',sans-serif;
        font-weight: 700;
    }

    .fa-quote-left:before {
        content: "\f10d";
    }
    .fa-quote-right:before {
        content: "\f10e";
    }

    .header-info .fa {
        display: inline-block;
        font: normal normal normal 14px/1 FontAwesome;
        font-size: inherit;
        text-rendering: auto;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }

    .header-info .top-social_v2 {
        height: 32px;
        margin-bottom: -15px;
        margin-left: 10px;
        list-style: none;
        display: inline-block;
    }
    .header-info .flipx {
        -moz-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        -o-transform: scaleX(-1);
        transform: scaleX(-1);
        filter: FlipH;
    }

    .header-info .top-social_v2 li {
        float: left;
        margin-right: 13px;
    }


    .header-info .top-social_v2 img {
        height: 35px;
        width: 35px;
        padding: 6px;
        background: 0 0;
    }

    .headertop-down {
        position: absolute;
        bottom: 50px;
        left: 50%;
        cursor: pointer;
        z-index: 5;
    }
    .headertop-down i {
        font-size: 28px;
        color: #fff;
        -ms-transform: scale(1.5,1);
        -webkit-transform: scale(1.5,1);
        transform: scale(1.5,1);
    }

    audio, canvas, progress, video {
        display: inline-block;
        vertical-align: baseline;
    }
    #bgvideo {
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        padding: 0;
        min-width: 99.999%;
        min-height: 550px;
        z-index: 0;
    }

    #video-btn, #video-add {
        position: absolute;
        bottom: 3px;
        right: 5px;
        width: 32px;
        height: 32px;
        z-index: 7;
        background-position: center;
        background-size: cover;
        cursor: pointer;
        opacity: .8;
        -moz-animation: poi-face 10s linear infinite alternate;
        -webkit-animation: poi-face 10s linear infinite alternate;
        -o-animation: poi-face 10s linear infinite alternate;
        animation: poi-face 10s linear infinite alternate;
    }

    .video-play, .loadvideo {
        background-image: url("https://cdn.jsdelivr.net/gh/moezx/cdn@3.1.9/img/Sakura/images/play@32x32.png");
    }

    #video-add {
        background-image: url(https://cdn.jsdelivr.net/gh/moezx/cdn@3.1.9/img/Sakura/images/add@32x32.png);
        bottom: 45px;
        display: none;
    }

    #video-add {
        display: none !important;
    }

    .video-pause {
        background-image: url(https://cdn.jsdelivr.net/gh/moezx/cdn@3.1.9/img/Sakura/images/pause@32x32.png);
    }

    .video-stu {
        position: absolute;
        bottom: -100px;
        left: 0;
        right: 0;
        margin: auto;
        padding: 6px 15px;
        text-align: center;
        color: #fff;
        width: 100%;
        background-color: rgba(0,0,0,.8);
        border-radius: 0;
        font-size: 18px;
        -webkit-transition: .4s ease all;
        -moz-transition: .4s ease all;
        -o-transition: .4s ease all;
        transition: .4s ease all;
    }