@keyframes totopopacity
{
from {opacity: 0;}
to {opacity: 1;}
}



@keyframes rotating {
    from {
    -ms-transform:rotate(0deg);
    -moz-transform:rotate(0deg);
    -webkit-transform:rotate(0deg);
    -o-transform:rotate(0deg);
    transform:rotate(0deg)
  }
  to {
    -ms-transform:rotate(360deg);
    -moz-transform:rotate(360deg);
    -webkit-transform:rotate(360deg);
    -o-transform:rotate(360deg);
    transform:rotate(360deg)
  }
  }


@-webkit-keyframes  vertical{
    from{-webkit-transform:translate(0,6px);-ms-transform:translate(0,6px);transform:translate(0,6px)}
    to{-webkit-transform:translate(0,-6px);-ms-transform:translate(0,-6px);transform:translate(0,-6px)
    }  
}
  
@keyframes vertical{
    from{-webkit-transform:translate(0,6px);-ms-transform:translate(0,6px);transform:translate(0,6px)}
    to{-webkit-transform:translate(0,-6px);-ms-transform:translate(0,-6px);transform:translate(0,-6px)
    }
  }
  @-webkit-keyframes  loadvertical{
    from{top:48%;}
    to{top: 52%;}  
}
  
@keyframes loadvertical{
    from{top:48%;}
    to{top: 52%;}
  }

  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @keyframes namemargin
  {
