@media (min-width: 992px){
    .d-lg-block {
        display: block!important;
    }
    .d-none {
        display: none!important;
    }
}

@media (min-width: 992px){
    .col-lg-3 {
        flex: 0 0 auto;
        width: 25%;
    }
}
.sidebar img{
    vertical-align: middle;
}
.homebody .sidebar{
    margin-top: 20px;
}
.row>* {
    flex-shrink: 0;
    width: 100%;
    max-width: 100%;
    padding-right: calc(var(--bs-gutter-x)/ 2);
    padding-left: calc(var(--bs-gutter-x)/ 2);
    margin-top: var(--bs-gutter-y);
}


.h-100 {
    height: 100%!important;
}

.sidebar-comment-text .emoji {
    height: 20px;
    width: auto;
}
.dark-mode .sidebar-box {
    border: 1px solid rgba(0, 0, 0, 0.2);
}
.p-3 {
    padding: 1rem!important;
}
.position-relative {
    position: relative!important;
}
.sidebar-box {
    border: 1px solid #e9ecef;
   
    min-height: 10rem;
    color: rgba(0,0,0,0.8);
    word-break: break-all;
    border-radius: var(--radius);
    margin-bottom: 1rem;
}



.sidebar-banner {
    object-fit: cover;
    background-position-x: center;
    background-position-y: center;
    background-size: cover;
    min-height: 120px;
    width: 100%;
    overflow: hidden;
    border-top-right-radius: 6px;
    border-top-left-radius: 6px;
    background-image: url("https://cdn.jsdelivr.net/gh/A-ANing/manifest_s@master/myblog/images/blogconfig/rigbg.jpg");
}

.sidebar-banner {
    position: relative;
}

.dark-mode .sidebar-banner::after {
    background: linear-gradient(to top, #1f1b24, transparent);
}
.sidebar-banner::after {
    content: '';
    width: 100%;
    height: 40%;
    position: absolute;
    bottom: 0;
    left: 0;
    background: linear-gradient(to top, var(--bs-white), transparent);
}

.text-center {
    text-align: center!important;
}
.sidebar{
    height: 100%;
}
.sidebar-avatar {
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    border: rgba(255, 255, 255, .4) 4px solid;
    width: 68px;
    height: 68px;
    border-radius: 50%;
    object-fit: cover;
    -o-object-fit: cover;
}
.position-absolute {
    position: absolute!important;
}

.sidebar-name {
    padding-top: 2.5rem;
    font-weight: bold;
    text-align: center;
    padding-bottom: .5rem;
}

.dark-mode .modal-footer, .dark-mode .sidebar-info {
    border-top: 1px dashed rgba(0, 0, 0, 0.5);
}
.sidebar-info {
    display: flex;
    padding: 1rem;
    border-top: 1px solid #efefef;
}

.sidebar-post-number, .sidebar-tags-number, .sidebar-categories-number {
    width: 33%;
    text-align: center;
    letter-spacing: 1px;
    font-size: 13px;
    line-height: 16px;
    font-weight: 200;
}
.sidebar-comment-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}
.mr-1 {
    margin-right: .25rem!important;
}
.sidebar-comment-text {
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    word-break: break-all;
}
.sidebar-comment-info {
    font-size: 12px;
    justify-content: space-between;
}
.d-flex {
    display: flex!important;
}
.w-100 {
    width: 100%!important;
}
a.a-none, .post-nav a {
    color: inherit;
    text-decoration: none;
}
.pb-3 {
    padding-bottom: 1rem!important;
}
.px-3 {
    padding-right: 1rem!important;
    padding-left: 1rem!important;
}
.d-flex {
    display: flex!important;
}
.sidebar-rand-date {
    font-size: 12px;
    color: white;
}
.sidebar-rand-title {
    font-size: 12px;
    color: white !important;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    word-break: break-word;
}
.sidebar-rand-info {
    z-index: 1;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1));
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
}

.sidebar-rand-img {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -o-object-fit: cover;
    object-fit: cover;
    background-position-x: center;
    background-position-y: center;
    background-size: cover;
}
.sidebar-rand-item {
    height: 7rem;
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    border-radius: 4%;
}
.p-2 {
    padding: .5rem!important;
}
#sidebar .sticky-top {
    position: sticky;
    top: 4rem;
    z-index: 5;
}
.dark-mode .sidebar-box {
    border: 1px solid rgba(0, 0, 0, 0.2);
}


#sidebar-box .sticky-top  {
    position: sticky;
    top: 4rem;
    z-index: 5;
}