.writeart-box{
    height: 100vh;

}

.writeart-textarea{
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    width: 100%;
    height: calc(100% - 100px);
    border: 0;
    padding: 10px  40px;
    overflow: auto;
    font-family: -apple-system,SF UI Text,Arial,PingFang SC,Hiragino Sans GB,Microsoft YaHei,WenQuanYi Micro Hei,sans-serif;
}

.writeart-left,.writeart-right{
    display: inline-block;
    width: 50%;
    height: 100%;
    vertical-align: middle;
}
.art_title{
    
    padding-bottom: 15px;
}
.writeart-right{
    padding: 20px;
    background-color: #fcfaf2;
    overflow: auto;
}
.writeart-btn{
    
    background-color: #d9d9d9;
    height: 30px;
    border-bottom: 1px solid #ccc;
}
.writeart-btn span{
    
    height: 30px;
    line-height: 30px;
    text-align: center;
    width: 110px;
    max-width: 50%;
    cursor: pointer;
}
.writeart-btn span:first-child{
    float: left;
    
}
.writeart-btn span:last-child{
    float: right;
    
}

.writeart-btn span:hover {
    color: #f2f2f2;
    background-color: #595959;
}
.writeart-box{
    border-top: 1px solid #eee;
}
.writeart-left{
    border-right: 1px solid #eee;
}
.writeart-left input{
    
    margin-top: 20px;
    -ms-flex-negative: 0;
    flex-shrink: 0;
    width: 100%;
    padding: 0 0 10px 40px;
    margin-bottom: 0;
    height: 50px;
    border: none;
    font-size: 30px;
    font-weight: 400;
    line-height: 30px;
    -webkit-box-shadow: none;
    box-shadow: none;
    color: #595959;
    background-color: transparent;
    outline: none;
    border-radius: 0;
    overflow: hidden;
    -o-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.writeart-box a{
    color: #2196F3;
}

.writeart-box ul {
    list-style: inside;
}


.highlight-wrap {
    position: relative;
    background: #21252b;
    border-radius: 5px;
    font: 15px/22px "Microsoft YaHei","Arial",Sans-Serif;
    line-height: 1.6;
    margin-bottom: 1.6em;
    max-width: 100%;
    
    text-shadow: none;
    color: #000;
    padding-top: 30px;
    box-shadow: 0 10px 30px 0px rgba(0,0,0,.4);
}
.highlight-wrap .point{
    cursor: pointer;
    width: 75px;
    height: 30px;
    display: block;
    position: absolute;
    top: 0;
}
@keyframes elastic{0%{transform:scale(0)}55%{transform:scale(1)}70%{transform:scale(.98)}100%{transform:scale(1)}}
.code-block-fullscreen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    min-width: 100%;
    z-index: 9999999;
    
    margin: 0;
    animation: elastic 1s;
}

.highlight-wrap .point::before {
    cursor: pointer;
    content: " ";
    position: absolute;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    background: #fc625d;
    width: 12px;
    height: 12px;
    left: 12px;
    margin-top: 10px;
    -webkit-box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
    box-shadow: 20px 0 #fdbc40, 40px 0 #35cd4b;
    z-index: 2;
}

.highlight-wrap .copy-code {
    cursor: pointer;
    color: #fff;
    position: absolute;
    float: right;
    right: 10px;
    top: 0;
    padding-top: 4px;
    padding-right: 2px;
    z-index: 2;
    font-size: 16px;
}
.fe-difference::before {
    content: "\f1c3";
}
.fe-intersect::before{
    content: "\f1c7"; 
}


.m {
    width: 100%;
    background-color: #fef5f4;
   
    margin-left: auto;
    margin-right: auto;
    line-height: 40px;
    /* padding-left: 15px; */
    /* margin-top: 100px; */
    position: relative;
    height: calc(100% - 70px);
}

.video-js .vjs-fullscreen-control {
    display: none;
}

.vjs-big-play-button {
    display: none !important;
}
.right_btn a{
    color: #fff;
  
}
.up-title{
    height: 50px;
    line-height: 2;
}
.left_word {

    display: inline-block;
    margin-left: 15px;
}
.nowimg,.nowimgload{
    border-radius: 2%;
    overflow: hidden;
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 9;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: center;
    background-position-x: center;
}
.nowimgload{
    z-index: 15;
    background-color: #eee;
}
.right_btn {
    height:300px;
    width: 100%;
    /* background-color: red; */
    top: 5px
}

.file {
    position: relative;
    display: inline-block;
    background: #eee;
    /* border: 1px solid #99D3F5; */
    border-radius: 2%;
    overflow: hidden;
    
    overflow: hidden;
    color: #ffffff;
    text-decoration: none;
    text-indent: 0;
    font-size: 15px;
    line-height: 20px;
    width: 100%;
    height: 100%;
}

.file::after{
    position: absolute;
    content: "";
    display: block;
    left: 50%;
    width:1px;
    top: 35%;
    height: 30%;
    background-color: #c5c5c5;
}
.up-title h1{
    text-align: center;
    font-size: 18px;
    color: #21252b;
}
.file::before{
    position: absolute;
    content: "";
    display: block;
    left: 35%;
    top: 50%;
    width: 30%;
    height: 1px;
    background-color: #c5c5c5;
}
.loadsvg{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    margin: 0 auto;
    display: block;
    background-color: #eee;
    animation: loadvertical 1.1s linear infinite;
}
.file input {
    position: absolute;
    display: block;
    right: 0;
    left: 0;
    top: 0;
    border-radius: 2%;
    overflow: hidden;
    bottom: 0;
    opacity: 0;
    width: 100%;
    z-index: 10;
    cursor: pointer;
}

.upimg-box{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
}

.upimg-box > div{
    border-radius: 10px;
    padding: 25px 30px;
    width: 400px;
    margin: 0 auto;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    -ms-transform:rotate(-50%); 	/* IE 9 */
    -moz-transform:rotate(-50%); 	/* Firefox */
    -o-transform:rotate(-50%); 	/* Opera */
    position: relative;
    background-color: #f5f5f5;
}
.subbtn_box{
    margin-top: 10px;
}
.subbtn:hover ,.btngo-on:hover{
    color: #404040;
    font-weight: 700;
    letter-spacing: 3px;
    background: none;
    -webkit-box-shadow: 0 5px 40px -10px rgba(0,0,0,.57);
    box-shadow: 0 5px 40px -10px rgba(0,0,0,.57);
    -webkit-transition: all .3s ease 0s;
    transition: all .3s ease 0s;
}
.subbtn ,.btngo-on{
    cursor: pointer;
    -webkit-animation: bottom20 .5s;
    animation: bottom20 .5s;
    border: none;
    background: #898c7b;
    color: #fff;
    font-weight: 500;
    padding: 6px 18px;
    text-transform: uppercase;
    border-radius: 4px;
    display: inline-block;
    -webkit-transition: all .3s ease 0s;
    transition: all .3s ease 0s;
}
.btngo-on{
    margin-left: 2%;
}
.subbtn{
    float: right;
    margin-right:2% ;
}