.avengers-tab--selected {
    border-width: 0;
    border-bottom-width: 3px;
    border-color: #e13f5e;
    border-style: solid;
}

li:focus{
    outline: 0;
}

.icon{
    -webkit-filter: drop-shadow(2px 4px 6px #ccc);
    filter: drop-shadow(2px 4px 6px #ccc);
}

#show-nav{
    cursor: pointer;
}
#show-nav .line {
    position: absolute;
    top: 7px;
    left: 50%;
    width: 28px;
    margin-left: -15px;
    height: 4px;
    background: #000;
    -webkit-transition: all .2s ease;
}
#show-nav .line1 {
    top: 17px;
}
.bigwinownav a i{
    padding-right: 4px;
}
.fa-chain:before, .fa-link:before {
    content: "\f0c1";
}

.faa-parent.animated-hover:hover>.icon-write, .faa-tada.animated-hover:hover {
    -webkit-animation: tada 2s linear infinite;
    animation: tada 2s linear infinite;
}

.bigwinownav{
    position: absolute;
    left: 0;
    right: 0;
    
    top: 0;
}
.bigwinownav>div{
    margin: 0 auto;
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
}
.header-user-avatar {
    position: relative;
    float: right;
    margin-left: 20px;
    line-height: 89px;
    height: 75px;
}
i.iconfont.js-toggle-search.iconsearch:hover {
    color: #FE9600;
}
i.iconfont.js-toggle-search.iconsearch {
    color: #666;
    cursor: pointer;
    font-size: 26px;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.searchbox {
    float: right;
    height: 75px;
    line-height: 75px;
    margin-left: 20px;
}
.searchbox, .site-top .lower, .header-user-avatar {
    animation: searchbox 1s;
}
.header-user-menu:before {
    content: "";
    position: fixed;
    top: 46px;
    right: 2.3%;
    margin-left: -10px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent #fff;
}
.no-logged a {
    display: initial;
    color: #69d2e7;
    font-size: 14px;
    padding: 0 5px;
    text-decoration: underline;
}
.no-logged {
    font-size: 13px;
    padding: 15px 10px;
    border-radius: 4px;
    border: 0;
}

.herder-user-name {
    font-size: 12px;
    background: #fff;
    padding: 10px;
    border-radius: 4px 4px 0 0;
}
.header-user-menu {
    position: absolute;
    width: 110px;
    right: -11px;
    text-align: left;
    background: 0 0;
    top: 66px;
    display: none;
    overflow: hidden;
    animation: header-user-menu .4s;
    line-height: 1.5;
    box-shadow: 0 1px 40px -8px rgba(0,0,0,.5);
    border-radius: 5px;
    text-align: center;
}

.header-user-avatar:hover .header-user-menu {
    display: block;
}

.fa-edit:before, .fa-pencil-square-o:before {
    content: "\f044";
}
.fa ,.icon-write{
    display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.fa-fort-awesome:before {
    content: "\f286";
}

.show-nav-box{

    animation: margin 1s ease-in-out forwards;
    -webkit-animation: margin 1s ease-in-out forwards;
    animation-iteration-count: 1;
    -webkit-animation-iteration-count: 1;
}
#show-nav {
    display: none;
    position: relative;
    float: right;
    margin-left: 20px;
    
    width: 30px;
    height: 33px;
    margin-top: 4.5px;
    cursor: pointer;
    z-index: 999;

}

#show-nav .line3 {
    top: 27px;
}

@media (max-width: 780px){
    #show-nav {
        display: block;
        opacity: 1;
        pointer-events: auto;
    }
    .bigwinownav{
    display: none;    
    }
    #live2d-widget{
        display: none;
    }
    #banner_wave_1,#banner_wave_2{
        display: none;
    }
    .tipimgBox{
        overflow: hidden;
    }
    .headerbox{
      
        height: 50px;
        flex-direction: row-reverse;
    }
 
    #show-nav .line  {
        width: 28px;
        height: 3px;
    }
    #show-nav .line1 {
        top: 16px;
    }
    #show-nav .line3 {
        top: 25px;
    }

    .NAME{
        font-size: 17px;
    }
}

.optionheader{
    background: rgba(255,255,255,0.9)!important;
    position: fixed;
    
    box-shadow: 0 1px 40px -8px rgba(0,0,0,.5);
}
.headerbox{
    
    transform:translateX(0px);
    -webkit-transform: translateX(0px);
    transition-duration: .5s;
}

@media (max-width: 780px){
    .header-user-avatar,.searchbox{
        display: none;
    }
    .box {
        animation: transX .5s  forwards;
   
    -webkit-animation: transX .5s  forwards;
    }
    }

@media(max-width:1265px){
.bigwinownav div>a{
    margin: 0 1rem!important;
}
}

@media(max-width:950px){
    .bigwinownav div>a{
        margin: 0 0.6rem!important;
    }
    .bigwinownav>div {
        margin: 0 111px 0 auto;
    }
    }
.box{
    /* transform:translateX(0px);
    -webkit-transform: translateX(0px);
    transition-duration: .5s; */
    
    position: relative;left:0%;
    /* overflow: hidden; */
}



.boxtransform{
    animation:transXZ .5s forwards!important;
    -webkit-animation: transXZ .5s  forwards;
    overflow: hidden;
}


.headerboxanimation{
    transform:translateX(75%);
    -webkit-transform: translateX(75%);
    transition-duration: .5s;
    overflow: hidden;
}



.navclose1{
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);
    background: #000;
    top:14px!important;
}


.navclose2{
   
    background: transparent!important;
}


.navclose3{
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    background: #000;
    top:15px!important;
}


.bigwinownav div>a:after {
    content: "";
    display: block;
    position: absolute;
    bottom: 20px;
    height: 6px;
    background-color: #fe9600;
    width: 100%;
    max-width: 0;
    border-radius: 4px;
    left: 0;
    transition: max-width .25s ease-in-out;
}
.bigwinownav a{
    
    position: relative;
}
.bigwinownav > div a{
    display: inline-block;
    height: 100%;
}
.bigwinownav a:hover{
    color: #FE9600!important;
    
    transition: color .25s ease-in-out;
   
}

.bigwinownav div>a:hover:after{
    max-width: 100%;
} 


 .about:hover ul {
    display: inline-block;
    -webkit-transition: all .4s;
    transition: all .4s;
    list-style: none;
}
.bigwinownav ul {
    display: none;
    opacity: 1;
    position: absolute;
    background: #fff;
    padding: 10px;
    top: 75px;
    right: -13px;
    width: 80px;
    text-align: center;
    z-index: 9999;
    border-radius: 5px;
    box-shadow: 0 1px 40px -8px rgba(0,0,0,.5);
    -webkit-animation: fadeInUp .3s .1s ease both;
    -moz-animation: fadeInUp .3s .1s ease both;
}

.bigwinownav  ul:before {
    content: "";
    position: absolute;
    top: -20px;
    left: 50%;
    margin-left: -10px;
    border-width: 10px;
    border-style: solid;
    border-color: transparent transparent #fff;
}

.bigwinownav ul li {
    width: 100%;
    
    line-height: 40px;

    margin: 0;
}
.site-top ul li {
    float: left;
    margin: 0 13px;
    position: relative;
    -webkit-transition: all 1s ease;
}

.site-top ul li a {
    padding: 10px 0;
    display: inline-block;
    color: #666;
}