
.myprogress {
  pointer-events: none;
  
  
 
  
}

.myprogress .bar {background: #ffc900;position: fixed;z-index: 1031;top: 0;
  left: 0;width: 100%;height: 2px;}
  .myprogress .peg {display: block;position: absolute;right: 0px;width: 100px;height: 100%;box-shadow: 0 0 10px #ffc900, 0 0 5px #ffc900;
    opacity: 1.0;-webkit-transform: rotate(3deg) translate(0px, -4px);
    -ms-transform: rotate(3deg) translate(0px, -4px);transform: rotate(3deg) translate(0px, -4px);}
    .myprogress  .myprogress-custom-parent {overflow: hidden;position: relative;}
    .myprogress .spinner-icon {width: 18px;height: 18px;box-sizing: border-box;
      border: solid 2px transparent;border-top-color: #ffc900;border-left-color: #ffc900;border-radius: 50%;
      -webkit-animation: myprogress-spinner 400ms linear infinite;
              animation: myprogress-spinner 400ms linear infinite;}


              .myprogress   .spinner {display: block;position: fixed;z-index: 1031;top: 15px;right: 15px;}

.myprogress-custom-parent .myprogress .spinner,
.myprogress-custom-parent .myprogress .bar {
  position: absolute;
}

