.login-box input{
    outline: none;
    padding-left: 10px;
    border-color: #D2D2D2!important;
    border-top-style: none;
    border-right-style: solid;
    border-bottom-style: solid;
    border-left-style: solid;
    height: 50px;
    width: 300px;
    border-radius: 0;
    padding-bottom: 0px;
    border-width:1px;
    padding-right: 30px;
}
.login-child-box :last-child input {
    padding-right: 110px;
    border-width:0;
}
.login-box{
    position: fixed;
    width: 100%;
    z-index: 99;
    height: 100%;
    top: 0;
    left: 0;
    flex-direction: column;
    display: flex;
    background: -moz-linear-gradient(top,  #5b6161 0%,#bacbd5 49%, #ffd09e 90%);
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,#5b6161), color-stop(49%,#bacbd5),color-stop(90%,#ffd09e));
    background: -webkit-linear-gradient(top, #5b6161 0%,#bacbd5 49%, #ffd09e 90%);
    background: -o-linear-gradient(top, #5b6161 0%,#bacbd5 49%, #ffd09e 90%);
    background: -ms-linear-gradient(top, #5b6161 0%,#bacbd5 49%, #ffd09e 90%);
    background: linear-gradient(135deg, #5b6161 0%,#bacbd5 49%, #ffd09e 90%);;
    justify-content: center;
    align-items: center;
}

.login-child-box{
    border-radius: 10px;
    border: 1px #eee dotted;
    width:300px;
   overflow: hidden;
    background-color: #eee;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login-child-box div {
    position: relative;
    
}
.login-child-box .codeimg {
    position: absolute;
    right: 10px;
    top: 0;
    cursor: pointer;

    border: none;
    width: 90px;
    height: 100%;
    display: flex;
   
    justify-content: center;
    align-items: center;
}
.login-child-box img {
    
   
    border: none;
    width: 90px;
    height: 30px;
}
.login-child-box .fe{
    position: absolute;
    right: 10px;
    line-height: 50px;
}
.fe-lock::before{
    content:"\f139";
    font-size: 20px;
}

.fe-user::before{
    content:"\f15a";
    font-size: 20px;
}
.admin-button:active{
    background-color: #000;
    border: 1px solid #fff;
}

.admin-button {
    margin-top: 20px;
    font-weight: bold;
    font-size: 18px;
    width: 300px;
    height: 50px;
    border-radius: 5px;
    background-color: #a78369;
    border: 1px solid #d8b29f;
    display: inline-block;
    height: 38px;
    line-height: 38px;
    padding: 0 18px;
   
    color: #fff;
    white-space: nowrap;
    text-align: center;
    font-size: 14px;
    
    
    cursor: pointer;
}