.article-box{
    padding-top: 80px;
    min-height:100vh ;
    
}

.article-mark a{
    color:#2196F3;
    word-wrap: break-word;
}


@media (max-width: 780px){

.article-box {
    padding-top: 50px !important;
}

}


.article-box i{
    font-size: 14px;
    margin-right: 0.4rem;
}
.article-box .art_title{
    padding: 0;
}
.article-box .article-meta{
    margin-bottom:5vh ;
}
.article-read i{
    vertical-align: middle;
    font-size: 16px;
}
.article-box .entry-census a {
    color: #fff;
}
.article-box .entry-census {
    color: #fff;
    font-size: 14px;
    padding: 18px 0 0;
    line-height: 39px;
    font-weight: 600;
    font-family: 'Noto Serif SC','Source Han Serif SC','Source Han Serif','source-han-serif-sc','PT Serif','SongTi SC','MicroSoft Yahei',Georgia,serif;
}
.article-box .entry-census span img {
    width: 35px;
    height: 35px;
    border-radius: 100%;
    float: left;
    margin-right: 12px;
}
.article-box .outer{
    background-color: #fcfaf2;
    min-height: 100vh;
    border-right-width: 1px;
    border-left-width: 1px;
    /* border: 0px; */
    border-top: 0px;
    border-bottom: 0px;
    border-style: #eee;
}
.article-box header{
    text-shadow: 2px 2px 10px #000;
}

.article-box header.single-header {
    max-width: 800px;
    padding: 0 10px;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
    top: auto;
    bottom: 20px;
}


.article-writer{
    font-size: 14px;
    float: right;
  
}


.article-writer:hover{
    color: #FE9600!important;
    font-weight: 600!important;
    transition: color .25s ease-in-out;
}

.article-writer:hover:after {
    max-width: 100%;
}

.article-writer:after {
    content: "";
    display: block;
    /* position: absolute; */
    /* bottom: -16px; */
    height: 5px;
    background-color: #fe9600;
    width: 100%;
    max-width: 0;
    border-radius: 4px;
    /* left: 0; */
    transition: max-width .25s ease-in-out;
}
.article-read{
    margin-left: 20px;
    color: #828282;
    font-size: 14px;
}




